import React from "react"
import { Link } from "gatsby"
import {TextField} from "@material-ui/core"
import Button from "@material-ui/core/Button"
import axios from 'axios'

import Layout from "../components/layout"
import SEO from "../components/seo"

class ThirdPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      jsonInput: '',
      pdfFilePath: '',
      hint: '',
      converted: 0
    }
  }

  handleJsonInputChanged(e) {
    this.setState({jsonInput: e.target.value})
  }

  handleConvertButtonClicked(e) {
    const jsonInput = this.state.jsonInput
    axios.post(
      'https://122.112.196.210:8888/jsonToConvert',
      jsonInput, { headers: { 'Content-Type': 'application/json' } }
    ).then( (response) => {
      this.setState({
        jsonInput: jsonInput,
        pdfFileName: response.data,
        hint: 'Ready for download ' + response.data,
        converted: 2
      })
    })

    let newState = Object.assign({}, this.state);
    newState.converted = 1;
    this.setState(newState);
  }

  renderHint() {
    const pdfLink = this.state.pdfFileName;
    let downloadHint
    if (this.state.converted === 2) {
      downloadHint = ( <a href={pdfLink} target="_blank"> PDF </a>)
    } else if (this.state.converted === 1) {
      downloadHint =( <p>  </p> )
    } else if (this.state.converted === 0) {
      downloadHint =( <p> JSON </p> )
    }
    return (
      <div>
        {downloadHint}
      </div>
    )
  }

  render() {
    return (
      <Layout headerTitle="Swagger2 PDF ">
        <SEO title="Swagger2 Json  PDF"/>
        <p>Swagger2 json</p>
        <div>
          <TextField id="filled-basic" label="JSON" variant="filled"
                     onChange={(e) => this.handleJsonInputChanged(e)}>
          </TextField>
        </div>
        <div>
          <Button variant="contained" color="primary" onClick={(e) => this.handleConvertButtonClicked(e)}>
            convert
          </Button>
        </div>

        <div>
          {this.renderHint()}
        </div>
      </Layout>
    )
  }
}

export default ThirdPage
